import { DateFormat } from '@lingui/react'
import { useEffectOnce } from '@speedlo/hooks'
import {
  closestIndexTo,
  eachDayOfInterval,
  isEqual,
  startOfDay,
} from 'date-fns'
import { isWithinInterval } from 'date-fns/fp'
import React, { Fragment, useEffect, useMemo, useState } from 'react'
import styled from 'styled-components'

import { createTimeSteps } from './createTimeSteps'
import { TimePickerButton } from './TimePickerButton'

interface IProps {
  minDate: Date
  maxDate: Date
  minuteStep: number
  onStepChange(step: Date): void
  initialTime?: Date
  filterIntervals?: Interval[]
}

const TimePickerContainer = styled.div`
  margin-top: 1rem;
`

const TimePickerDay = styled.div`
  font-size: 1rem;
  font-weight: bold;
  text-align: left;
  margin: 0 0 0.2rem 0.2rem;
`

export const TimePicker: React.FC<IProps> = ({
  minDate,
  maxDate,
  minuteStep,
  onStepChange,
  initialTime,
  filterIntervals = [],
}) => {
  const steps = useMemo(
    () => {
      const allSteps = createTimeSteps(minDate, maxDate, minuteStep)
      if (filterIntervals.length) {
        return allSteps.filter(step =>
          filterIntervals.some(isWithinInterval(step)),
        )
      }
      return allSteps
    },
    [minDate, maxDate, minuteStep, filterIntervals],
  )

  const days = useMemo(
    () => {
      return eachDayOfInterval({ start: minDate, end: maxDate })
    },
    [minDate, maxDate],
  )

  const [stepIdx, setStepIdx] = useState(0)

  useEffect(
    () => {
      if (initialTime) {
        setStepIdx(closestIndexTo(initialTime, steps))
      }
    },
    [initialTime],
  )

  useEffectOnce(() => {
    onStepChange(steps[stepIdx])
  })

  return (
    <TimePickerContainer>
      {days.map(day => (
        <Fragment key={day.getDate()}>
          <TimePickerDay>
            <DateFormat
              value={day}
              format={{ month: 'long', day: 'numeric', weekday: 'long' }}
            />
          </TimePickerDay>
          {steps.map((step, idx) =>
            isEqual(day, startOfDay(step)) ? (
              <TimePickerButton
                key={step.getTime()}
                time={step}
                isSelected={idx === stepIdx}
                canBeSelected={true}
                onClick={() => {
                  setStepIdx(idx)
                  onStepChange(step)
                }}
              />
            ) : null,
          )}
        </Fragment>
      ))}
    </TimePickerContainer>
  )
}
