import { reaction } from 'mobx'
import { useDisposable } from 'mobx-react-lite'

import { useRoot } from '../../common/WithRoot'

export function useCartRefresh(onRefresh: () => void) {
  const { order: orderModel } = useRoot()
  return useDisposable(() =>
    reaction(
      () => orderModel.shouldRefreshCart,
      () => {
        orderModel.setCartRefresh(false)
        onRefresh()
      },
    ),
  )
}
