import { Trans } from '@lingui/react'
import React from 'react'
import { Route } from 'react-router-dom'
import { Alert } from 'reactstrap'

import { useRoot } from '../../common/WithRoot'
import { buildQuery } from '../../graph'
import { GOrderCartForItem } from '../../graph/types'
import { RM } from '../../helpers/ramda'
import { OrderCartForItemQuery } from '../../models/OrderModel.graphql'
import { CartModal } from './CartModal'

const QueryCart = buildQuery<
  GOrderCartForItem.Query,
  GOrderCartForItem.Variables
>(OrderCartForItemQuery)

export const ChangeCartModal: React.FC = () => {
  const { order: orderModel } = useRoot()
  return (
    <Route
      path="/order/change/:itemId"
      render={({ match, history }) => (
        <QueryCart
          variables={{ itemId: match.params.itemId }}
          renderLoading={false}
          renderError={() => (
            <Alert color="warning">
              <Trans>Error in loading cart item. Issue was reported.</Trans>
            </Alert>
          )}
          render={(data, { loading }) => (
            <CartModal
              recipe={data.item && data.item.recipe}
              initialSelection={
                data.item
                  ? (data.item.sideDishes.map(
                      RM.path(['recipe', 'id']),
                    ) as string[])
                  : []
              }
              onConfirm={sideDishes =>
                orderModel.updateItem(match.params.itemId, sideDishes)
              }
              onClose={() => history.goBack()}
              isLoading={loading}
            />
          )}
        />
      )}
    />
  )
}
