import React from 'react'
import { ButtonProps } from 'reactstrap'
import styled from 'styled-components'

import { PickerButton } from '../../common/PickerButton'
import { GCartRecipe } from '../../graph/types'

interface IProps extends ButtonProps {
  sidedish: GCartRecipe.SideDishes
  isSelected: boolean
  canBeSelected: boolean
}

const Name = styled.div`
  float: left;
  text-align: left;
  white-space: normal;
  max-width: 13rem;
`

const Price = styled.div`
  font-weight: bold;
  float: right;
`

export const SidedishCheckbox: React.FC<IProps> = ({
  sidedish,
  ...buttonProps
}) => (
  <PickerButton
    size="xs"
    outline={true}
    width="17.5rem"
    data-testid={`sidedishCheckbox__${sidedish.id}`}
    {...buttonProps}
  >
    <Name>{sidedish.nameLabel}</Name>
    <Price>{sidedish.price.formattedValue}</Price>
  </PickerButton>
)
