import { useDebounceCallback, useRouterContext } from '@speedlo/hooks'
import React, { useCallback, useEffect } from 'react'
import { Form, IFormApi, IFormState } from 'react-form'
import { Form as StrapForm } from 'reactstrap'

import { useRoot } from '../../common/WithRoot'
import { PaymentGateEnum } from '../../graph/types'
import { logOrder } from '../../helpers/logger'
import { RM } from '../../helpers/ramda'
import { reportError } from '../../helpers/sentry'
import { TAgreement, TOrderModel } from '../../models/OrderModel'

interface IProps {
  render(formApi: IFormApi & IFormState<IOrderFormState>): ReactNode
}

export interface IOrderFormState {
  firstname: string
  lastname: string
  email: string
  phonePrefix: string
  phone: string
  note: string
  payment: PaymentGateEnum
  agreements: TAgreement[]
}

function getFormValues(orderModel: TOrderModel): IOrderFormState {
  return {
    firstname: orderModel.firstname,
    lastname: orderModel.lastname,
    email: orderModel.email,
    phone: orderModel.phone,
    phonePrefix: orderModel.phonePrefix || orderModel.defaultPhonePrefix,
    note: orderModel.note,
    payment: PaymentGateEnum.Gopay,
    agreements: [],
  }
}

const debounceInterval = 1500

export const OrderFormSetup: React.FC<IProps> = ({ render }) => {
  const { order: orderModel } = useRoot()
  const { history } = useRouterContext()

  useEffect(() => {
    if (!orderModel.isVamInitialized) {
      orderModel.initVam()
    }
  }, [])

  const updateVam = useCallback(
    async (formState: IFormState<IOrderFormState>, formApi: IFormApi) => {
      try {
        await orderModel.updateVam(formState.values)
        formApi.setValue(
          'agreements',
          mergeFormAgreements(
            formState.values.agreements,
            orderModel.agreements,
          ),
        )
      } catch (err) {
        reportError(err, {
          formState: formState.values,
        })
      }
    },
    [],
  )

  const onChange = useDebounceCallback(updateVam, debounceInterval)

  return (
    <Form
      defaultValues={getFormValues(orderModel)}
      validateOnMount={true}
      onChange={onChange}
      onSubmit={async (formState: IOrderFormState) => {
        try {
          await orderModel.submitOrder(formState)
          history.replace(`/finish`)
        } catch (e) {
          logOrder(e)
        }
      }}
    >
      {formApi => (
        <StrapForm onSubmit={formApi.submitForm}>{render(formApi)}</StrapForm>
      )}
    </Form>
  )
}

function mergeFormAgreements(
  formValue: TAgreement[],
  agreements: TAgreement[],
) {
  return agreements.map(agreement => {
    const formAgreement = formValue.find(RM.propEq('id', agreement.id))
    if (formAgreement) {
      return { ...agreement, isConfirmed: formAgreement.isConfirmed }
    }
    return agreement
  })
}
