import './CartView.css'

import React from 'react'
import { useQuery } from 'react-apollo-hooks'

import { GCartView } from '../../graph/types'
import { AddToCartModal } from './AddToCartModal'
import { CartContents } from './CartContents'
import { CartIsEmpty } from './CartIsEmpty'
import { CartViewQuery } from './CartView.graphql'
import { ChangeCartModal } from './ChangeCartModal'
import { useCartRefresh } from './useCartRefresh'

interface IProps {
  orderId: MaybeID
}

export const CartView: React.FC<IProps> = ({ orderId }) => {
  const { data, refetch } = useQuery<GCartView.Query, GCartView.Variables>(
    CartViewQuery,
    { suspend: false, skip: !orderId, variables: { orderId: orderId! } },
  )

  useCartRefresh(refetch)

  return (
    <>
      <AddToCartModal />
      <ChangeCartModal />
      {!data || !data.order || data.order.countWares === 0 ? (
        <CartIsEmpty />
      ) : (
        <CartContents order={data.order} />
      )}
    </>
  )
}
