import React, { useRef } from 'react'

import { useRoot } from '../../common/WithRoot'
import { CartItemButton } from './CartItemButton'

interface IProps {
  itemId: ID
  enabled: boolean
}

export const RemoveCartItemButton: React.FC<IProps> = ({ itemId, enabled }) => {
  const { order: orderModel } = useRoot()
  const clickedRef = useRef(false)
  return (
    <CartItemButton
      icon="delete"
      color="#99230e"
      enabled={enabled && !clickedRef.current}
      data_testid={`removeCartItemButton`}
      onClick={() => {
        if (!clickedRef.current) {
          orderModel.removeItemFromCart(itemId)
        }
        clickedRef.current = true
      }}
    />
  )
}
