import gql from 'graphql-tag'

import { OrderFragment } from '../cart/CartView.graphql'

export const OrderTimePickerQuery = gql`
  query GOrderTimePicker($branchId: ID!) {
    companyBranch(id: $branchId) {
      id
      isOpen
      businessHours: businessHoursIntervals {
        openAt
        closeAt
        closed
      }
      defaultDeliveryTime
      defaultPickupTime
      willOpenAt
      willCloseAt
    }
  }
`

export const UpdateOrderDeliveryTimeMutation = gql`
  mutation MUpdateOrderDeliveryTime($orderId: ID!, $deliverAt: DateTime) {
    updateOrder(id: $orderId, order: { deliverAt: $deliverAt }) {
      ...GOrder
    }
  }
  ${OrderFragment}
`
