import { Trans } from '@lingui/react'
import React from 'react'
import { Route } from 'react-router-dom'
import { Alert } from 'reactstrap'

import { Mobile } from '../../common/Responsive'
import { useRoot } from '../../common/WithRoot'
import { buildQuery } from '../../graph'
import { GOrderCartForRecipe } from '../../graph/types'
import { OrderCartForRecipeQuery } from '../../models/OrderModel.graphql'
import { CartModal } from './CartModal'

const QueryCart = buildQuery<
  GOrderCartForRecipe.Query,
  GOrderCartForRecipe.Variables
>(OrderCartForRecipeQuery)

export const AddToCartModal: React.FC = () => {
  const { order: orderModel } = useRoot()
  return (
    <Route
      path="/order/add/:recipeId"
      render={({ match, history }) => (
        <QueryCart
          variables={{ recipeId: match.params.recipeId }}
          renderLoading={false}
          renderError={() => (
            <Alert color="warning">
              <Trans>Error in loading cart item. Issue was reported.</Trans>
            </Alert>
          )}
          render={(data, { loading }) => (
            <Mobile>
              {isMobile => (
                <CartModal
                  recipe={data.recipe}
                  onConfirm={sideDishes => {
                    if (isMobile) {
                      orderModel.addFromRecipeMobile(
                        match.params.recipeId,
                        sideDishes,
                      )
                      // let the optimistic response happen in this case
                      return Promise.resolve()
                    }
                    return orderModel.addFromRecipe(
                      match.params.recipeId,
                      sideDishes,
                    )
                  }}
                  onClose={() => history.goBack()}
                  isLoading={loading}
                />
              )}
            </Mobile>
          )}
        />
      )}
    />
  )
}
