import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../common/WithRoot'
import { GCartView } from '../../graph/types'
import { CartDiscounts } from './CartDiscounts'
import { CartItem } from './CartItem'
import { CartTotals } from './CartTotals'

interface IProps {
  order: GCartView.Order
}

export const CartContents: React.FC<IProps> = ({ order }) => {
  const { order: orderModel } = useRoot()

  return useObserver(() => (
    <div className="cart__items__container">
      <div className="cart__items__container__content">
        {order.items.map(item => (
          <CartItem
            key={item.id}
            item={item}
            isActive={orderModel.lastActiveRecipeId === item.recipe.id}
            onEdit={() => orderModel.setUsedRecipeId(item.recipe.id)}
          />
        ))}
        <CartDiscounts order={order} />
        <CartTotals order={order} />
      </div>
    </div>
  ))
}
